import { ajax, toast } from 'nc-lightapp-front';

import { PAGECODE, AREA, BUTTONID } from '../../constance';
import excelImportconfig from '../../../../../uap/common/components/excelImportconfig';

export function onSearchAfterEvent(field, val, sarchid) {
    this.props.search.getSearchValByField('search', 'idissys');
    let meta = this.props.meta.getMeta();
    if (field == 'idissys') {
        meta[AREA.searchId].items.find((item) => {
                if ("pk_mid_ref" == item.attrcode) {
                        item.queryCondition = {
                                key: 'pk_mid_ref',
                                idissys: val,
                                GridRefActionExt: "nccloud.web.hpf.itfdocref.refsqlbuilder.BDRefInfoSqlBuilder"
                        }
                }
        });
    }
    if (field == 'pk_mid_ref') {
        let idissys = this.props.search.getSearchValByField('search', 'idissys').value.firstvalue;
        meta[AREA.searchId].items.find((item) => {
                if ("pk_hrp_ref" == item.attrcode) {
                        item.queryCondition = {
                                key: 'pk_hrp_ref',
                                pk_mid_ref: val.refname,
                                idissys: idissys ? idissys : null,
                                GridRefActionExt: "nccloud.web.hpf.itfdocref.refsqlbuilder.BDRefInfoSqlBuilder"
                        }
                }
        });
    }
    this.props.meta.setMeta(meta);

    if (field == 'pk_org') {
        let pk_org = this.props.search.getSearchValByField('search','pk_org').value.firstvalue;
        let excelconfig = excelImportconfig(this.props, 'hpf', 'itfdocref',true,'', {appcode: this.props.getSearchParam('c'), pagecode: PAGECODE.cardPagecode, pk_org: pk_org});
        this.props.button.setUploadConfig('Import', excelconfig);
    }

    this.props.table.setAllTableData(AREA.listTableId, { rows: [] });

}
