
import { URL, FIELDS, UISTATUS } from '../../constance';

export default function(props, record, index) {
        props.pushTo(URL.card, 
                { status: UISTATUS.edit, 
                  id: record[FIELDS.pk_head_fields].value,
                  idissys: record[FIELDS.idissys].value,
                  pk_mid_ref: record[FIELDS.pk_mid_ref].value,
                  pk_hrp_ref: record[FIELDS.pk_hrp_ref].value
                });
}
