import { BUTTONID ,PAGECODE} from '../../constance';
import searchBtnClick from './searchBtnClick';
import refreshBtnClick from './refreshBtnClick';
import deleteBtnClick from './deleteBtnClick';
import addBtnClick from './addBtnClick';
import editBtnClick from './editBtnClick';
import copyBtnClick from './copyBtnClick';
import excelImportconfig from '../../../../../uap/common/components/excelImportconfig';

function buttonClick(props, id, text,  record, index) {
        switch (id) {
                case BUTTONID.Add:
                        addBtnClick.call(this, props);
                        break;
                case BUTTONID.Edit:
                        editBtnClick.call(this, props, record, index);
                        break;
                case BUTTONID.Delete:
                        deleteBtnClick.call(this, props, record, index);
                        break;
                case BUTTONID.Copy:
                        copyBtnClick.call(this, props, record, index);
                        break;
                case BUTTONID.Refresh:
                        refreshBtnClick.call(this, props);
                        break;
                case BUTTONID.Export:
                        exportBtnClick.call(this, props, record, index);
                        break;
        }
}

function exportBtnClick(props, record, index){
        let pks = [];
        //获取界面中的数据主键放入到pks中，若不用导出数据则不用处理 start
        //...pks组装代码
        //获取界面中的数据主键放入到pks中，若不用导出数据则不用处理 end
        this.setState({pks: pks},()=>{
                props.modal.show('exportFileModal');//'exportFileModal'为固定写法
        });
}

function importBtnClick(props){
        let pk_org = props.search.getSearchValByField('search','pk_org').value.firstvalue;
        let excelconfig = excelImportconfig(props, 'hpf', 'itfdocref',true,'', {appcode: props.getSearchParam('c'), pagecode: PAGECODE.cardPagecode, pk_org: pk_org});
        props.button.setUploadConfig('Import',excelconfig);
}


export { buttonClick, searchBtnClick };
