import { asyncComponent } from 'nc-lightapp-front';
import DiscountList from '../list';

const DiscountCard = asyncComponent(() =>
        import(/*webpackChunkName: "multiple/basebille/card/card"*/ /*webpackMode: "eager" */ '../card')
);

const routes = [
        {
                path: '/',
                component: DiscountList,
                exact: true
        },
        {
                path: '/list',
                component: DiscountList
        },
        {
                path: '/card',
                component: DiscountCard
        }
];

export default routes;
