
import { PAGECODE, AREA, BUTTONID, DATASOURCECACHE, DEFCACHEKEY} from '../../constance';
import { getListDisableHotKeyBtn } from '../../tool/hotKeysUtil';
import { buttonClick } from '../btnClicks';
import { buttonControl } from '../viewControl/buttonControl';
import { getLangByResId } from '../../tool/multiLangUtil';

import excelImportconfig from '../../../../../uap/common/components/excelImportconfig';
import commonSearch from '../btnClicks/commonSearch';
import { setDefData } from '../../tool/cacheDataManager';

export default function(props) {
        props.createUIDom(
                {
                        pagecode: PAGECODE.listPagecode,
                },
                data => {
                        if (data) {
                                if (data.button) {
                                        let button = data.button;
                                        props.button.setButtons(button);
                                        props.button.setPopContent(
                                                BUTTONID.Delete,
                                                getLangByResId(this, '4001PUBMESSAGE-0000012')
                                        ); /* 国际化处理： 确定要删除吗？*/
                                        buttonControl(props);
                                        // 刷新按钮
                                        // let cacheData = getDefData(DATASOURCECACHE.dataSourceListCacheKey, DEFCACHEKEY.queryCacheKey);
                                        // props.button.setDisabled(BUTTONID.Refresh, cacheData ? false : true);
                                }
                                if (data.template) {
                                        let meta = data.template;
                                        modifierMeta.call(this, props, meta);
                                        props.meta.setMeta(meta);
                                }
                                // let pk_org;
                                // if (data.context && data.context.pk_org) {
                                //         pk_org = data.context.pk_org;
                                // }
                                let pk_org = props.search.getSearchValByField(AREA.searchId, 'pk_org');
                                let excelconfig = excelImportconfig(props, 'hpf', 'itfdocref',true,'', {appcode: props.getSearchParam('c'), pagecode: PAGECODE.cardPagecode, pk_org: pk_org.value.firstvalue});
                                props.button.setUploadConfig('Import', excelconfig);
                                //添加默认查询
                                let queryInfo = props.search.getQueryInfo(AREA.searchId, false); //必输项为空时，返回值为false;
                                if (queryInfo.querycondition.conditions && queryInfo.querycondition.conditions.length>0) {
                                        // 缓存查询条件
                                        setDefData(DATASOURCECACHE.dataSourceListCacheKey, DEFCACHEKEY.queryCacheKey, queryInfo);
                                        // 查询
                                        commonSearch.call(this, props, queryInfo);
                                }
                        }

                }
        );
}

function modifierMeta(props, meta) {

        //添加操作列
        meta[AREA.listTableId].items.push({
                label: getLangByResId(this, '4001PUBMESSAGE-0000006') /* 国际化处理： 操作*/,
                itemtype: 'customer',
                attrcode: 'opr',
                width: '180px',
                visible: true,
                fixed: 'right',
                render: (text, record, index) => {
                        let buttonAry = [BUTTONID.Edit, BUTTONID.Delete];
                        return props.button.createOprationButton(buttonAry, {
                                area: AREA.listInner,
                                ignoreHotkeyCode: getListDisableHotKeyBtn(),
                                buttonLimit: 3,
                                onButtonClick: (props, key) => buttonClick.call(this, props, key, text, record, index),
                        });
                },
        });
        //搜索参照过滤
        meta[AREA.searchId].items.map((item) => {
		if (item.attrcode === 'pk_mid_ref') {
			item.queryCondition = () => {
                                return { GridRefActionExt: 'nccloud.web.hpf.itfdocref.refsqlbuilder.MidRefSearchSqlBuilder' };
                        };
                }
                if (item.attrcode === 'pk_org') { //财务组织过滤
                        item.queryCondition = () => {
                            return {
                                funcode: props.getSearchParam('c'),//appcode获取
                                //用户权限过滤
                                TreeRefActionExt: 'nccloud.web.refer.sqlbuilder.PrimaryOrgSQLBuilder'
                            };
                        };
                    }
        });
        return meta;
}


